.underlinedinput {
    position: relative;
}
.underlinedinput input {
    color: transparent !important;
    position: relative;
    z-index: 1;
}
.underlinedinput-carat {
    position: relative;
}
.underlinedinput-carat::before {
    position: absolute;
    left: .1em;
    top: 0;
    right: .2em;
    bottom: .1em;
    content: "";
    background-color: black;
}
.underlinedinput input:focus + .underlinedinput-view .underlinedinput-carat {
    animation: blinker 1s step-start infinite;
}
.underlinedinput-view {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 0;
}
.underlinedinput-underline {
    position: absolute;
    left: 0;
    top: .3em;
    z-index: 0;
}
.underlinedinput-placeholder {
    opacity: .5;
}
.underlinedinput-typography {
    font-family: 'Press Start';
}
@keyframes blinker {
    50% {
        opacity: 0;
    }
}